import axios from 'axios';

const BASE_URL = 'https://pokeapi.co/api/v2';


export function getPokemon(pokemonId){
    return axios.get(`${BASE_URL}/pokemon/${pokemonId}`)
        .then(response => response.data)
}

export function getPokemonSpecies(pokemonSpeciesURL){
    return axios.get(`${pokemonSpeciesURL}`)
        .then(response => response.data)
}



