import React from 'react';
import { Link } from 'react-router-dom';


export default class PokeBall extends React.Component {

    render() {
        return (
            <div className="row">
                <div className="col-sm-1" style={{ float: "none", margin: "0 auto" }}>
                    <Link>
                        <img alt="pokeball" onClick={this.props.onClick} height="100px" width="100px" src={require('../images/pokeball.png')} />
                    </Link>
                </div>

            </div>
        )
    }
}