import React from 'react';
import { Link } from 'react-router-dom';

export default class DetailNavbar extends React.Component {

    render() {
        var linkStyle = {
            color: "#000",
            textDecoration: "none",
        }

        var prevUrl = process.env.PUBLIC_URL + '/pokemon/' + this.props.prevPokemon;
        var nextUrl = process.env.PUBLIC_URL + '/pokemon/' + this.props.nextPokemon;
        var prevPokemon = this.props.prevPokemon === 0 ? null : <Link to={prevUrl} title="previous" style={linkStyle}>
            <i className="glyphicon glyphicon-chevron-left" />
                <h3>#{this.props.prevPokemon}</h3>
        </Link>
        var nextPokemon = <Link to={nextUrl} title="next" style={linkStyle}>
            <i className="glyphicon glyphicon-chevron-right" />
                <h3>#{this.props.nextPokemon}</h3>
        </Link>

        return(
            <div className="row" style={{background: "#0099cc"}}>
                <div className="col-6 nav-item" style={{textAlign:"left"}}>{prevPokemon}</div>
                <div className="col-6 nav-item" style={{textAlign:"right"}}>{nextPokemon}</div>
            </div>
        )
    }
}