import React from 'react';
import PokemonType from "./pokemonType";
import {Link} from 'react-router-dom';

export default class PokemonItem extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            pokemon: {}
        }
    }

    formatId = (id) => {
		if (id.length === 1) {
			return "#00" + id;
		} else if (id.length === 2) {
			return "#0" + id;
		} else {
			return "#" + id;
		}
    }

    render(){
        var captionStyle = {
			marginTop: -18
		};

		var containerStyle = {
			background: "#FFF",
			padding: 10,
			border: "12px solid #EEE",
			height: 298
		};

		var pokemonIdStyle = {
			opacity: 0.5,
			fontSize: 16,
			fontWeight: 700
		};

        var types = [];
        if(this.props.pokemonType !== undefined)
        {
            this.props.pokemonType.forEach(function (item, index) {
                types.push(<PokemonType pokemonType={item.type.name} key={index} colSize="col-5" border="2px solid #FFF"/>);
            });
        }
        return (
            <div style={containerStyle} className="col-sm-3">
                <div className="row">
                    <div className="col-sm-12" style={{ display: 'table-cell', verticalAlign: 'middle', textAlign: 'center' }}>
                        <Link to={this.props.detailsUrl} className="thumbnail">
                            <img src={this.props.pokemonImgUrl} alt="pokemon" />
                        </Link>
                    </div>
                </div>

                <div style={captionStyle} className="caption">
                    <div className="row">
                        <div className="col-sm-12">
                            <p style={pokemonIdStyle} >{this.formatId(this.props.pokemonId)}</p>
                            <h3>{this.props.pokemonName}</h3>
                            {types}
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}