import React from 'react';
import { Link } from 'react-router-dom';
import PokemonType from './pokemonType';

export default class MyPokemonItem extends React.Component {
    uppercaser(str) {
        return str.replace(str[0], str[0].toUpperCase());
    }

    render() {
        const { pokemon } = this.props;

        var thumbnailStyle = {
            height: 130
        };

        var types = [];
        pokemon.type.forEach((element,index) => {
            types.push(<PokemonType key={index} pokemonType={element.type.name} colSize="col-sm-4" border="3px solid #EEE"/>);
        });

        return (
            <div className="card" style={{ marginBottom: "10px" }}>
                <div className="card-body">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-sm-2" style={{ display: 'table-cell', verticalAlign: 'middle', textAlign: 'center' }}>
                                <Link to={this.props.pokemon.detailsUrl} className="thumbnail" style={thumbnailStyle}>
                                    <img src={this.props.pokemon.imgUrl} alt="pokemon" />
                                </Link>
                            </div>
                            <div className="col-sm-3">
                                <h4 className="card-title">{this.uppercaser(pokemon.nickName)}</h4>
                                <p>{this.uppercaser(pokemon.name)}</p>
                            </div>
                            <div className="col-sm-3">
                                {types}
                            </div>
                            <div className="col-sm-1 offset-sm-2">
                                <button className="btn btn-sm btn-warning"
                                    onClick={() => this.props.release(pokemon)}>Release</button>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}