import { getPokemon } from "../services/repository";
import MyPokemonItem from "./myPokemonItem";
import React from 'react';


export default class MyPokemonList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            myPokemonList: []
        }
    }

    componentDidMount = async () => {
        let myPokemon = JSON.parse(localStorage.getItem('myPokemon'));
        if (!myPokemon) return;
        var myPokemonList = await this.getMyPokemonList(myPokemon);
        this.setState({ myPokemonList });
    }

    getMyPokemonList = async (myPokemon) => {
        var pokemonPromises = [];

        var pokemonIdList = myPokemon.map(i => {
            return i.id;
        }).filter((v, i, a) => a.indexOf(v) === i);

        pokemonIdList.forEach(i => {
            pokemonPromises.push(getPokemon(i))
        })
        var pokemonDetails = await Promise.all(pokemonPromises)
            .then(data => {
                var updated = data.map((pokemon, index) => {
                    return {
                        name: pokemon.name,
                        type: pokemon.types,
                        id: pokemon.id,
                        imgUrl: pokemon.sprites.front_default,
                        detailsUrl: process.env.PUBLIC_URL + '/pokemon/' + pokemon.id
                    }
                });
                return updated;
            });

        var myPokemonList = []
        myPokemon.forEach(element => {
            var x = pokemonDetails.find(pokemon => pokemon.id == element.id);
            x.nickName = element.nickName;
            myPokemonList.push({
                name: x.name,
                nickName: element.nickName,
                id: x.id,
                imgUrl: x.imgUrl,
                detailsUrl: x.detailsUrl,
                type: x.type
            });
        });
        return myPokemonList;
    }


    release = async (pokemonIndex) => {
        let myPokemon = JSON.parse(localStorage.getItem('myPokemon'));
        myPokemon.splice(pokemonIndex, 1);
        localStorage.setItem('myPokemon', JSON.stringify(myPokemon));
        var myPokemonList = await this.getMyPokemonList(myPokemon);
        this.setState({ myPokemonList });
    }

    render() {
        const { myPokemonList } = this.state;
        return (
            <div className="container">
                <h3 className="card-title">My Pokemon</h3>
                {myPokemonList.map((pokemon, index) => <MyPokemonItem pokemon={pokemon} release={this.release.bind(this, index)} key={index} />)}
            </div>
        )
    }
}