import React from 'react';

export default class PokemonCharacteristic extends React.Component {
    getHeight = (height) => {
        return (height / 3.846153846).toFixed(1) + "'";
    }

    getWeight = (weight) => {
        return (weight / 4.545454545).toFixed(1) + " lbs";
    }

    render() {

        var wellStyle = {
            background: "#0099cc",
            minHeight: 158
        };

        var headerStyle = {
            color: "#FFF",
            fontWeight: 700
        };

        var infoStyle = {
            marginTop: -5
        };

        var belowColStyle = {
            marginTop: 10
        };

        return (
            <div style={wellStyle} className="col-sm-6">
                <div className="row">
                    <div className="col-sm-6">
                        <p style={headerStyle}>Height</p>
                        <h4 style={infoStyle}>{this.getHeight(this.props.height)}</h4>
                    </div>
                    <div className="col-sm-6">
                        <p style={headerStyle}>Base Exp.</p>
                        <h4 style={infoStyle}>{this.props.baseExp}</h4>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6" style={belowColStyle}>
                        <p style={headerStyle}>Weight</p>
                        <h4 style={infoStyle}>{this.getWeight(this.props.weight)}</h4>
                    </div>
                    <div className="col-sm-6" style={belowColStyle}>
                        <p style={headerStyle}>Abilities</p>
                        <h4 style={infoStyle}>
                            {this.props.ability}
                        </h4>
                    </div>
                </div>

                
            </div>
        );
    }
}