import React, { Component } from 'react';
import './App.css';
import { BrowserRouter as Router, Link, Route, withRouter } from 'react-router-dom';
import MyPokemonList from './components/myPokemonList';
import PokemonList from './components/pokemonList';
import PokemonDetail from './components/pokemonDetail';
import { Navbar, Nav } from 'react-bootstrap';

class App extends Component {
  componentDidMount(){
    document.title = "David's Pokemon GO"
  }

  render() {
    return (
      <Router>
        <div>
          <Navbar variant="dark" bg="dark" expand="lg">
            <Navbar.Brand as={Link} to={process.env.PUBLIC_URL+'/'}>David's Pokemon GO</Navbar.Brand>
            <Navbar.Toggle aria-controls="david-navbar" />
            <Navbar.Collapse id="david-navbar">
              <Nav className="mr-auto">
                <Nav.Link as={Link} to={process.env.PUBLIC_URL+'/pokemon'}>Pokemon List</Nav.Link>
                <Nav.Link as={Link} to={process.env.PUBLIC_URL+'/mypokemon'}>My Pokemon</Nav.Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          <div className="container">
            <br />
            <Route exact path={process.env.PUBLIC_URL+'/'} component={PokemonList} />
            <Route exact path={process.env.PUBLIC_URL+'/pokemon'} component={PokemonList} />
            <Route exact path={process.env.PUBLIC_URL+'/pokemon/:pokemonId'} component={withRouter(PokemonDetail)} />
            <Route exact path={process.env.PUBLIC_URL+'/mypokemon'} component={MyPokemonList} />
          </div>
        </div>
      </Router>
    )
  }
}

export default App;
