import React from 'react';
import { Modal } from 'react-bootstrap'

export default class CatchModal extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            isCaught: false,
            inputValue: '',
            details: {}
        }
    }

    componentWillReceiveProps = (nextProps) => {
        if (this.props.show !== nextProps.show) {
            this.setState({ showModal: nextProps.show })
        }
    }

    close = () => {
        this.setState({ showModal: false, inputValue: '' });
    }

    open = (details) => {
        var isCaught = Math.random() < 0.5;
        this.setState({ showModal: true, isCaught: isCaught, details: details });
    }

    updateInputValue(evt) {
        this.setState({
            inputValue: evt.target.value
        });
    }

    submitPokemon = () => {
        let myPokemon = localStorage.getItem('myPokemon')
            ? JSON.parse(localStorage.getItem('myPokemon')) : [];
        myPokemon.push({ id: this.state.details.id, nickName: this.state.inputValue })
        localStorage.setItem('myPokemon', JSON.stringify(myPokemon));
        this.close();
    }

    render = () => {
        var modal = this.state.isCaught === true ?
            <Modal.Body>
                <h4>{this.state.details.name + ' is Caught !!'}</h4>
                <p>
                    {'Give name to your ' + this.state.details.name + ' :'}
                    <input type="text" value={this.state.inputValue} onChange={evt => this.updateInputValue(evt)} />
                    <button className="btn btn-sm btn-success"
                        onClick={this.submitPokemon}>Submit</button>
                </p>
            </Modal.Body> :
            <Modal.Body>
                <h4>{this.state.details.name + ' escaped !!'}</h4>
                <p>
                    {'Better luck next time !'}
                </p>
            </Modal.Body>
        return (
            <Modal
                onHide={this.close}
                show={this.state.showModal}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter">
                        Catch Result
                    </Modal.Title>
                </Modal.Header>
                {modal}
            </Modal>
        );
    }
}