import React from 'react';

export default class PokemonDescription extends React.Component {
    render() {
        var style = { fontWeight: 700 };

        var imgStyle = {
            margin: "0 auto",
            display: "block"
        };


        return (
            <div className="row">
                <div className="col-sm-6">
                    <img style={imgStyle} src={this.props.imgUrl} alt="pokemon" className="img-responsive" />
                </div>
                <div className="col-sm-6">
                    <p style={style}>{this.props.pokemonDescription}</p>
                </div>
            </div>
        );
    }
}