import React from 'react';
import PokemonType from './pokemonType';

export default class PokemonTypes extends React.Component {
    render(){
        if(!this.props.pokemonType) return null;

        var types = [];

        this.props.pokemonType.forEach((element,index) => {
            types.push(<PokemonType key={index} pokemonType={element.type.name} colSize="col-sm-4" border="3px solid #EEE"/>);
        });

        return (
            <div className="col-sm-6">
                <div className="row">
                    <div className="col-sm-12">
                        <h4>Types</h4>
                        {types}
                    </div>

                </div>
            </div>
        );
    }
}