import React from 'react';
import PokemonMove from './pokemonMove';
import { Accordion, Card } from 'react-bootstrap';

export default class PokemonMoves extends React.Component {
    render() {
        if (!this.props.pokemonMoves) return null;

        var moves = [];
        this.props.pokemonMoves.forEach((element, index) => {
            moves.push(<PokemonMove key={index} move={element.move.name} type={element.type} />);
        });

        return (
            <div className="row" style={{ marginTop: "30px" }}>

                <div className="col-sm-12">
                    <h4>Moves</h4>
                    <Accordion>
                        <Card>
                            <Accordion.Toggle as={Card.Header} eventKey="0">
                                <h5 style={{textAlign:"center"}}>Click to View Pokemon's Move</h5>
                            </Accordion.Toggle>
                            <Accordion.Collapse eventKey="0">
                            <Card.Body>
                            <div className="row">
                                    {moves}
                                </div>
                            </Card.Body>
                                
                            </Accordion.Collapse>
                        </Card>
                    </Accordion>

                </div>
            </div>
        )
    }

}