import React from 'react';

export default class PokemonNameId extends React.Component {
    formatId = (id) => {
		if (id.length === 1) {
			return "#00" + id;
		} else if (id.length === 2) {
			return "#0" + id;
		} else {
			return "#" + id;
		}
	}

	render() {
		if (!this.props.pokemonId) return null;

		var headerStyle = {
			textAlign: "center",
			fontWeight: 700,
			opacity: 0.8
		};

		var idStyle = {
			opacity: 0.5
		};

		var divStyle = {
			paddingBottom: 70,
			marginTop:30
		};

		return (
			<div style={divStyle} className="col-12">
        <h1 style={headerStyle}>{this.props.pokemonName} <span style={idStyle}> {this.formatId(this.props.pokemonId.toString())}</span></h1> 
			</div>   
		);
	}
}