import React from 'react';

export default class PokemonStats extends React.Component {

    render() {

        var hpStyle = {
			width: this.props.hp + "%",
			background: "#0099cc"
		};

        var attackStyle = {
            width: this.props.attack + "%",
            background: "#0099cc",
        };

        var defenseStyle = {
            width: this.props.defense + "%",
            background: "#0099cc",
        };

        var spatkStyle = {
            width: this.props.sp_atk + "%",
            background: "#0099cc"
        };

        var spdefStyle = {
            width: this.props.sp_def + "%",
            background: "#0099cc"
        };

        var speedStyle = {
            width: this.props.speed + "%",
            background: "#0099cc"
        };

        var special = {
            marginTop: 13
        };

        var progressStyle = {
            position: "relative"
        };

        var spanStyle = {
            position: "absolute",
            display: "block",
            width: "100%",
            fontWeight: 700,
            color: "#000"
        };

		return (
			<div className="row" style={{marginTop: "30px"}}>
				<div className="col-sm-6">
          <div className="col-sm-12">
            <div className="col-sm-4">
              <p>HP</p>
            </div>
            <div className="col-sm-10">
              <div style={progressStyle} className="progress">
                <div className="progress-bar progress-bar-custom" role="progressbar" style={hpStyle}>
                  <span style={spanStyle}>{this.props.hp}</span>
                </div>
              </div>
            </div>
          </div>

          <div style={special} className="col-sm-12">
            <div className="col-sm-4">
              <p>Attack</p>
            </div>
            <div className="col-sm-10">
              <div style={progressStyle} className="progress">
                <div className="progress-bar progress-bar-custom" role="progressbar" style={attackStyle}>
                  <span style={spanStyle}>{this.props.attack}</span>
                </div>
              </div>
            </div>
          </div>

          <div style={special} className="col-sm-12">
            <div className="col-sm-4">
              <p>Defense</p>
            </div>
            <div className="col-sm-10">
              <div style={progressStyle} className="progress">
                <div className="progress-bar progress-bar-custom" role="progressbar" style={defenseStyle}>
                  <span style={spanStyle}>{this.props.defense}</span>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="col-sm-6">
          
          <div className="col-sm-12">
            <div className="col-sm-4">
              <p>Special Attack</p>
            </div>
            <div className="col-sm-10">
              <div style={progressStyle} className="progress">
                <div className="progress-bar progress-bar-custom" role="progressbar" style={spatkStyle}>
                  <span style={spanStyle}>{this.props.sp_atk}</span>
                </div>
              </div>
            </div>
          </div>

          <div style={special} className="col-sm-12">
            <div className="col-sm-4">
              <p>Special Defense</p>
            </div>
            <div className="col-sm-10">
              <div style={progressStyle} className="progress">
                <div className="progress-bar progress-bar-custom" role="progressbar" style={spdefStyle}>
                  <span style={spanStyle}>{this.props.sp_def}</span>
                </div>
              </div>
            </div>
          </div>

          <div style={special} className="col-sm-12">
            <div className="col-sm-4">
              <p>Speed</p>
            </div>
            <div className="col-sm-10">
              <div style={progressStyle} className="progress">
                <div className="progress-bar progress-bar-custom" role="progressbar" style={speedStyle}>
                  <span style={spanStyle}>{this.props.speed}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
			</div>
		);
    }
}