import React from 'react';
import { getPokemon, getPokemonSpecies } from '../services/repository';
import PokemonTypes from './pokemonTypes';
import PokemonNameId from './pokemonNameId';
import PokemonStats from './pokemonStats';
import PokemonDescription from './pokemonDescription';
import PokemonCharacteristics from './pokemonCharacteristic';
import DetailNavbar from './detailNavbar';
import PokeBall from './pokeBall';
import CatchModal from './catchModal';
import PokemonMoves from './pokemonMoves';

export default class PokemonDetail extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            details: {}
        };
    }

    componentWillMount = async() => {
        var details = await this.getDetails(this.props.match.params.pokemonId);
        this.setState({details: details});
    }
    
    componentWillReceiveProps = async(nextProps) => {
        if (nextProps.match.params.pokemonId !== this.props.match.params.pokemonId) {
            var details = await this.getDetails(nextProps.match.params.pokemonId);
            this.setState({ details: details });
        }
    }
    
    getDetails = async(pokemonId) => {
        var details = await getPokemon(pokemonId).then(async(data) => {
            var pokemon = {};
            pokemon.name = data.name;
            pokemon.type = data.types;
            pokemon.moves = data.moves;
            pokemon.id = data.id;
            pokemon.imgUrl = data.sprites.front_default;
            pokemon.ability = data.abilities.find((i) => i.is_hidden === false).ability.name;
            pokemon.height = data.height;
            pokemon.weight = data.weight;
            pokemon.baseExp = data.base_experience;
            pokemon.attack = data.stats.find((i) => i.stat.name === "attack").base_stat;
            pokemon.defense = data.stats.find((i) => i.stat.name === "defense").base_stat;
            pokemon.hp = data.stats.find((i) => i.stat.name === "hp").base_stat;
            pokemon.sp_atk = data.stats.find((i) => i.stat.name === "special-attack").base_stat;
            pokemon.sp_def = data.stats.find((i) => i.stat.name === "special-defense").base_stat;
            pokemon.speed = data.stats.find((i) => i.stat.name === "speed").base_stat;
            
            pokemon.description = await getPokemonSpecies(data.species.url).then((speciesData) => {
                return speciesData.flavor_text_entries.find((i) => i.language.name === "en").flavor_text;
            });

            pokemon.moves = await Promise.all(data.moves.map(async i => {
                var type = await getPokemonSpecies(i.move.url).then((moveData) => {
                    return moveData.type.name;
                })
                return {move: i.move, type: type};
            })); 

            return pokemon;
        });
        return details;
    }

    catch = () => {
        this.child.open(this.state.details);
    }


    render(){
        document.body.style.background = "#EEE";

        var containerStyle = {
            backgroundColor: "#fff",
            borderTop: 0,
            boxShadow: "0 0 3px rgba(0,0,0,0.4)",
            paddingBottom: 11,
        }

        return (
            <div className="container" style={containerStyle}>
                <DetailNavbar prevPokemon={this.state.details.id - 1} nextPokemon={this.state.details.id + 1} />
                <PokemonNameId pokemonName={this.state.details.name} pokemonId={this.state.details.id} />
                <PokemonDescription pokemonDescription={this.state.details.description} imgUrl={this.state.details.imgUrl} />
                <div className="row" style={{marginTop:"30px"}}>
                    <PokemonTypes pokemonType={this.state.details.type} />
                    <PokemonCharacteristics
                        weight={this.state.details.weight}
                        height={this.state.details.height}
                        baseExp={this.state.details.baseExp}
                        ability={this.state.details.ability}
                        abilityDescription={this.state.details.abilityDescription}
                    />
                </div>
                <PokemonMoves pokemonMoves={this.state.details.moves} />

				<PokemonStats
					hp={this.state.details.hp}
					attack={this.state.details.attack}
					defense={this.state.details.defense}
					sp_atk={this.state.details.sp_atk}
					sp_def={this.state.details.sp_def}
					speed={this.state.details.speed}
				/>
                <br/><hr/>
                <PokeBall onClick={this.catch.bind(this)}/>
                <br/>
                <CatchModal ref={(ref) => { this.child = ref; }} />
			</div>
        )
    }
} 