import React from 'react';

export default class SearchField extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: "",
            hidden: "hidden",
            isSearched: false
        }
    }

    onChange = (e) => {
        this.setState({ value: e.target.value });
    }

    onSubmit = (e) => {
        e.preventDefault();
        if (this.state.value) {
            this.setState({ isSearched: true });
            this.props.onSearch(this.state.value);
            this.setState({ value: "" });
            this.setState({ hidden: "" });
        }
    }

    onClick = () => {
        this.setState({ isSearched: false });
        this.setState({ hidden: "hidden" });
        this.props.onReset();
    }

    render() {

        var headerStyle = {
            background: "#303030",
            color: "#FFF",
            paddingBottom: 10,
            paddingLeft: 35,
            paddingTop: 30
        };

        return (
            <div className="container">
                <div style={headerStyle} className="header">
                    <div className="row">
                        <div className="col-sm-12 col-12">
                            <h3>Name, Number or Type</h3>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-5 col-10">
                            <form action="" method="" onSubmit={this.onSubmit} >
                                <div className="input-group">
                                    <div className="input-group">
                                        <input
                                            type="text"
                                            className="form-control"
                                            placeholder="Search.."
                                            value={this.state.value}
                                            onChange={this.onChange}
                                        />
                                        <button onClick={this.onSubmit} className="btn btn-primary input-group-addon">Search</button>
  
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-sm-5" style={{marginTop:"20px", marginBottom:"10px"}}>
                        <button onClick={this.onClick} className="btn btn-warning sharp">Reset</button>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}