import { getPokemon } from "../services/repository";
import PokemonItem from "./pokemonItem";
import React from 'react';
import SearchField from "./searchField";

export default class PokemonList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pokemons: [],
            caught: 0
        }
    }

    componentDidMount = async () => {
        var pokemons = await this.getPokemonList(1, 301);
        let caughtCount = JSON.parse(localStorage.getItem('myPokemon')) !== null ? JSON.parse(localStorage.getItem('myPokemon')).length : 0;
        this.setState({ pokemons: pokemons, caught: caughtCount })
    }

    getPokemonList = async (start, end) => {
        var pokemonPromises = [];
        for (var i = start; i < end; i++) {
            pokemonPromises.push(getPokemon(i))
        }
        return await Promise.all(pokemonPromises)
            .then(data => {
                var updated = data.map((pokemon, index) => {
                    return {
                        name: pokemon.name,
                        type: pokemon.types,
                        id: pokemon.id,
                        imgUrl: pokemon.sprites.front_default,
                        detailsUrl: process.env.PUBLIC_URL + '/pokemon/' + pokemon.id
                    }
                });
                return updated;
            });
    }

    search = (searchQuery) => {
        searchQuery = searchQuery.toString().toLowerCase();

        // eslint-disable-next-line
        var pokemons = this.state.pokemons.filter(function (item) {
            if (item.name.toString().toLowerCase().indexOf(searchQuery) > -1 || item.id.toString().indexOf(searchQuery) > -1) {
                return true;
            } else if (item.type[0].type.name.toString().toLowerCase() === searchQuery) {
                return true;
            } else if (typeof item.type[1] !== "undefined" && item.type[1].type.name === searchQuery) {
                return true;
            }
        });
        let caughtCount = JSON.parse(localStorage.getItem('myPokemon')).length;
        this.setState({ pokemons: pokemons, caught: caughtCount })
    }

    resetSearch = async () => {
        var pokemons = await this.getPokemonList(1, 301);
        this.setState({ pokemons })
    }

    render() {
        document.body.style.background = "#EEE";
        var margin = { marginTop: 30 };
        let { pokemons } = this.state;
        return (
            <div>
                <SearchField onSearch={this.search.bind(this)} onReset={this.resetSearch.bind(this)} />
                <div className="container" style={margin}>
                    <h3 className="card-title">Pokemon List | ({this.state.caught} is Caught)</h3>
                    <div className="row">
                        {pokemons.map((pokemon, index) => <PokemonItem
                            key={index}
                            pokemonId={pokemon.id}
                            pokemonName={pokemon.name}
                            pokemonImgUrl={pokemon.imgUrl}
                            pokemonType={pokemon.type}
                            detailsUrl={pokemon.detailsUrl} />
                        )}
                    </div>
                    <hr />
                </div>
            </div>

        )
    }

}